import React from 'react';
import { Legend } from './Legend'

const flourishStyle = (interactive, active, flourishFullScreen) => {
    if (interactive && active && flourishFullScreen) {
        return {zIndex: "11 !important", opacity: 1}
    } else if (!active) {
        return {opacity: 0}
    } else if (interactive && !active && flourishFullScreen) {
        return {opacity: 0}
    }
}

export const Chapter = ({ id, theme, title, image, flourish, embed, description, header, body, link, currentChapterID, legend, interactiveMap }) => {
    const classList = id === currentChapterID ? "step active" : "step";
    const flourishFullScreen = flourish && flourish.fullScreen ? true : false
    const active = id === currentChapterID

    const flourishStyling = flourishStyle(interactiveMap, active, flourishFullScreen)

    return (
        <div id={id} className={classList}>
            <div className={theme}>
                {title &&
                    <h2 className="title">{title}</h2>
                }
                {image &&
                    <img src={image} alt={title}></img>
                }
                {description &&
                    <p>{description}</p>
                }
                {header &&
                    <h3>{header}</h3>
                }
                {body &&
                    <p>{body}</p>
                }
                {/* {flourish &&
                    <div 
                        style={flourishStyling}
                        // style={flourishFullScreen && (id === currentChapterID) ? { opacity: 1 } : { opacity: 0 }} 
                        className={flourishFullScreen ? "flourish-embed full-screen" : "flourish-embed feature"} 
                        data-src={`visualisation/${flourish.id}`}
                    />
                } */}
                {flourish &&
                    <div className="flourish-container">
                        <iframe 
                            src={`https://public.flourish.studio/visualisation/${flourish.id}/embed?auto=1`} 
                            style={flourishStyling}
                            title={id} 
                            className={flourishFullScreen ? "flourish-embed full-screen" : "flourish-embed feature"} 
                            frameBorder="0"
                        />
                    </div>
                }
                {embed &&
                    <iframe src={embed} title={id} className="embed" width="100%" height="400" frameBorder="0" />
                }
                {link &&
                    <a href={link.url} target='_blank' rel="noopener noreferrer">{link.title}</a>
                }
                {legend &&
                    <Legend title={legend.title} layout={legend.layout} />
                }
            </div>
        </div>
    )
}