import React from 'react'
import ExploreOffIcon from '@material-ui/icons/ExploreOff';
import ExploreIcon from '@material-ui/icons/Explore';

import IconButton from '@material-ui/core/IconButton';
import Tooltip from '@material-ui/core/Tooltip';

export const ToogleMapInteractivity = (props) => {
    return (
        <div className="map-interactive" onClick={props.clicked}>
            <Tooltip title={props.isActive ? 'Disable interactive map' : 'Enable interactive map'} placement="left">
                <IconButton aria-label="delete">
                    {props.isActive ? <ExploreOffIcon fontSize="large" /> : <ExploreIcon fontSize="large" />}
                </IconButton>
            </Tooltip>
        </div>
    )
}
