import React from 'react'

const signatureStyle = {
    point: { width: 10, height: 10, borderRadius: '50%' },
    line: { width: 20, height: 3 },
    polygon: { width: 20, height: 20 }
}

const signature = (layout) => {
    return (
        <table>
            <tbody>
                {layout.map(l => (
                    <tr key={l.text}>
                        <td>
                            <div
                                className="legend-signature"
                                style={{ ...signatureStyle[l.geomType], backgroundColor: l.color }}
                            />
                        </td>
                        <td>
                            <span>{l.text}</span>
                        </td>
                    </tr>
                ))}
            </tbody>
        </table>
    )
}

export const Legend = (props) => {
    let Signatures = signature(props.layout)

    return (
        <div className="legend">
            <h3>{props.title}</h3>
            {Signatures}
        </div>
    )
}